import { ComponentChildren } from 'preact';
import { keys } from 'ramda';
import { stringLiteralArray } from '../utils/UNSAFE_arrayUtils';
import { cssProps } from '@oracle/oraclejet-cssprops';
import { mergeInterpolations } from '../utils/UNSAFE_mergeInterpolations';

export const subheadingElementTypes = stringLiteralArray([
  'h1',
  'h2',
  'h3',
  'h4',
  'h5',
  'h6',
  'div',
  'span'
]);

type ElementType = typeof subheadingElementTypes[number];

type SubheadingProps = {
  /**
   * Specifies the subheading tag. When a heading tag should not be used 'as' may be set to div or span, for example in collection group headers (such as list view group headers).
   */
  as: ElementType;

  /**
   * Specifies the font size and line height.   */

  size?: 'xs' | 'sm' | 'md' | 'lg' | 'xl' | '2xl' | 'inherit';

  /**
   * Specifies the subheader id
   */
  id?: string;

  /**
   * Specifies the children
   */
  children: ComponentChildren;
};

const headingsizeMap = {
  h1: '2xl',
  h2: 'xl',
  h3: 'lg',
  h4: 'md',
  h5: 'sm',
  h6: 'xs',
  div: 'md',
  span: 'md'
};

const styles = {
  base: cssProps`${{
    margin: 0,
    /* @ts-ignore */
    '& > *': {
      marginTop: '0 !important',
      marginBottom: '0 !important',
      marginLeft: '0 !important',
      marginRight: '0 !important'
    }
  }}`,
  size: {
    xs: cssProps`${{
      fontSize: 'var(--oj-c-PRIVATE-DO-NOT-USE-typography-subheading-xs-font-size)',
      fontWeight: 'var(--oj-c-PRIVATE-DO-NOT-USE-typography-subheading-xs-font-weight)',
      lineHeight: 'var(--oj-c-PRIVATE-DO-NOT-USE-typography-subheading-xs-line-height)'
    }}`,
    sm: cssProps`${{
      fontSize: 'var(--oj-c-PRIVATE-DO-NOT-USE-typography-subheading-sm-font-size)',
      fontWeight: 'var(--oj-c-PRIVATE-DO-NOT-USE-typography-subheading-sm-font-weight)',
      lineHeight: 'var(--oj-c-PRIVATE-DO-NOT-USE-typography-subheading-sm-line-height)'
    }}`,
    md: cssProps`${{
      fontSize: 'var(--oj-c-PRIVATE-DO-NOT-USE-typography-subheading-md-font-size)',
      fontWeight: 'var(--oj-c-PRIVATE-DO-NOT-USE-typography-subheading-md-font-weight)',
      lineHeight: 'var(--oj-c-PRIVATE-DO-NOT-USE-typography-subheading-md-line-height)'
    }}`,
    lg: cssProps`${{
      fontSize: `var(--oj-c-PRIVATE-DO-NOT-USE-typography-subheading-lg-font-size)`,
      fontWeight: 'var(--oj-c-PRIVATE-DO-NOT-USE-typography-subheading-lg-font-weight)',
      lineHeight: 'var(--oj-c-PRIVATE-DO-NOT-USE-typography-subheading-lg-line-height)'
    }}`,
    xl: cssProps`${{
      fontSize: 'var(--oj-c-PRIVATE-DO-NOT-USE-typography-subheading-xl-font-size)',
      fontWeight: 'var(--oj-c-PRIVATE-DO-NOT-USE-typography-subheading-xl-font-weight)',
      lineHeight: 'var(--oj-c-PRIVATE-DO-NOT-USE-typography-subheading-xl-line-height)'
    }}`,
    '2xl': cssProps`${{
      fontSize: 'var(--oj-c-PRIVATE-DO-NOT-USE-typography-subheading-2xl-font-size)',
      fontWeight: 'var(--oj-c-PRIVATE-DO-NOT-USE-typography-subheading-2xl-font-weight)',
      lineHeight: 'var(--oj-c-PRIVATE-DO-NOT-USE-typography-subheading-2xl-line-height)'
    }}`,
    inherit: cssProps`${{
      fontSize: 'inherit',
      fontWeight: 'inherit',
      lineHeight: 'inherit'
    }}`
  }
};

export const sizeOptions = keys(styles.size);

const interpolations = [
  () => ({ class: styles.base }),
  ({ size = '2xl' }: Pick<SubheadingProps, 'size'>) => ({
    class: styles.size[size]
  })
];

const styleInterpolations = mergeInterpolations<Omit<SubheadingProps, 'children'>>(interpolations);

export const Subheading = ({ children, ...props }: SubheadingProps) => {
  const Component = props.as;
  //@ts-ignore
  props.size = props.size ?? headingsizeMap[Component];
  const { class: cls, ...style } = styleInterpolations(props);
  return (
    <Component class={cls} style={style} id={props.id}>
      {children}
    </Component>
  );
};
